:root {
    --view-bg: #2f2f2f;
}

.App {
    text-align: center;
    align-items: center;
}

#title {
    padding: 2rem;
}

.boxes-container {
    display: flex;
    flex-direction: column;
    margin-top: 1.5rem;
    font-size: 1.3rem;
    color: red;
    width: 90%;
    margin: auto;
}

#editor {
    resize: auto;
    margin-top: 4rem;
}

#preview {
    margin-top: 1.5rem;
    text-align: left;
}

.view {
    width: 100%;
    height: 80vh;
    background-color: var(--view-bg);
    color: var(--ed-txt-color);
    padding: 1rem;
    border-radius: 0.8rem;
    overflow: auto;
}

img {
    width: 100%;
}

@media screen and (min-width: 768px) {
    .boxes-container {
        flex-direction: row;
        width: 80%;
        gap: 3%;
        padding: 0;
        margin: auto;
        place-items: center;
    }

    #editor {
        margin-top: -0.3rem;
    }

    .view {
        width: 48.5%;
    }

    #preview {
        margin-top: 0;
    }
}
